const SELECT_GOODS_SEARCH_LOADING = 'SELECT_GOODS/SELECT_GOODS_SEARCH/SELECT_GOODS_SEARCH_LOADING';
const SELECT_GOODS_SEARCH_FAIL = 'SELECT_GOODS/SELECT_GOODS_SEARCH/SELECT_GOODS_SEARCH_FAIL';
const SELECT_GOODS_SEARCH_SUCCESS = 'SELECT_GOODS/SELECT_GOODS_SEARCH/SELECT_GOODS_SEARCH_SUCCESS';

const SELECT_GOODS_SEARCH_DATA = 'SELECT_GOODS/SELECT_GOODS_SEARCH/SELECT_GOODS_SEARCH_DATA';

export default function (state = {loading:false}, action = {}) {
  switch (action.type) {
    case SELECT_GOODS_SEARCH_LOADING:
     return {
        ...state,
       loading: true,
       loaded: false,
     };
    case SELECT_GOODS_SEARCH_SUCCESS:
      debugger;
      return {
        ...state,
        loading: false,
        loaded: true,
        data: action.result
      };
    case SELECT_GOODS_SEARCH_FAIL:
      debugger;
      return {
        ...state,
        loading: false,
        loaded: false,
        error: action.msg
      };
    case SELECT_GOODS_SEARCH_DATA:
      debugger;
      return{
        ...state,
        searchdata:action
      }
    default:
      return state
  }
}

export function selectGoodsSearch (values) {
  return {
    types: [SELECT_GOODS_SEARCH_LOADING, SELECT_GOODS_SEARCH_SUCCESS, SELECT_GOODS_SEARCH_FAIL],
    promise: (client) => client.get('/item/seller/supplier/querySellSupplyItems', {params: values})
    /*promise: (client) => client.get('/supplyTestData.json',{params:values})*/
  }
}

export function saveFormData (values) {
  debugger;
  return {
    type:SELECT_GOODS_SEARCH_DATA,
    searchdata: values
  }
}